.player-container {
  background-color: #f6f6f6;
}
.player-container .player {
  width: 700px;
  padding-left: 10px;
  padding-bottom: 40px;
  margin: 0 auto;
  padding-top: 100px;
}
.player-container .player .caption {
  font-size: 16px;
  padding: 20px 0;
}
.player-container .player .caption a {
  text-decoration: none;
  color: #2f9842;
}
.player-container .player .music-title {
  font-size: 25px;
  font-weight: 400;
  color: #030303;
  height: 36px;
  padding: 20px 0;
}
.player-container .player .music-artist {
  padding: 10px 0;
  font-size: 15px;
  font-weight: 400;
  color: #4a4a4a;
}
.player-container .player .volume-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.player-container .player .volume-container .left-time {
  font-size: 14px;
  font-weight: 400;
  color: #999;
  width: 60px;
}
.player-container .player .volume-container .volume-wrapper {
  opacity: 0;
  -webkit-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}
.player-container .player .volume-container .volume-wrapper:hover {
  opacity: 1;
}
.player-container .player .progress-container {
  margin-bottom: 30px;
}
.player-container .player .icon {
  display: inline-block;
  width: 32px;
  height: 32px;
  background: url(/image/icons.png) no-repeat;
  background-size: 32px;
  margin: 3px 5px;
}
.player-container .player .icon-prev {
  margin-left: 0;
  background-position: 0 0;
}
.player-container .player .icon-pause {
  background-position: 0 -64px;
}
.player-container .player .icon-play {
  background-position: 0 -96px;
}
.player-container .player .icon-next {
  background-position: 0 -32px;
}
.player-container .player .icon-volume {
  display: inline-block;
  width: 16px;
  height: 16px;
  /*  background是CSS简写属性，用来集中设置各种背景属性，可以用来设置一个或多个属性：background-color,
        background-image,background-position,background-repeat,background-size,background-attachment*/
  background: url(/image/icons.png) no-repeat;
  background-position: 0 -112px;
  background-size: 16px;
}
.player-container .player .icon-nosound {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(/image/nosound.jpg) no-repeat;
  background-position: 0 0;
  background-size: 16px;
}
.player-container .player .icon-cycle {
  background-position: 0 -160px;
}
.player-container .player .icon-once {
  background-position: 0 -128px;
}
.player-container .player .icon-random {
  background-position: 0 -192px;
}
.player-container .player .repeat {
  float: right;
}
.player-container .player .cover {
  float: right;
  width: 180px;
  height: 180px;
  margin-left: 20px;
}
.player-container .player .cover img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
}



