.music-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 36px;
  line-heiht: 36px;
  font-size: 12px;
  border-bottom: 1px solid #ddd;
  padding-left: 30px;
  cursor: pointer;
}
.music-item .delete {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: url(/image/delete.jpg) no-repeat;
  background-size: 20px;
  opacity: 0;
  -webkit-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
  margin-left: auto;
  margin-right: 15px;
}
.music-item .delete:hover {
  opacity: 1;
}
.music-item.focus {
  color: #f00;
}
.music-item:hover {
  background-color: #ccc;
}



